.admin {
  padding-top: 40px;
  padding-bottom: 40px;
}

.admin__container.log {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  max-width: 320px;
  display: flex;
}

.admin__login, .admin__password {
  color: #222021;
  background-color: #0000;
  border: 1px solid #222021;
  width: 100%;
  padding: 8px 40px 8px 24px;
  font-size: 24px;
  font-weight: 300;
}

.admin__login {
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
}

.admin__password {
  border-bottom-right-radius: 24px;
  border-bottom-left-radius: 24px;
}

.admin__btn {
  color: #222021;
  cursor: pointer;
  background-color: #fee400;
  border: 2px solid #0000;
  border-radius: 48px;
  width: 120px;
  padding: 10px 0;
  transition: all .3s ease-in-out;
}

@media (hover: hover) {
  .admin__btn:hover {
    color: #222021;
    background-color: #0000;
    border: 2px solid #222021;
  }
}

.items-list {
  flex-direction: column;
  align-items: start;
  margin: 0 auto 20px;
  display: flex;
}

.items-list__item {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.admin__form {
  flex-direction: column;
  align-items: center;
  max-width: 320px;
  margin: 0 auto;
  display: flex;
}

.admin__form input, .admin__images {
  color: #222021;
  background-color: #0000;
  border: 1px solid #222021;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px 24px;
  font-size: 24px;
  font-weight: 300;
}

.admin__images {
  cursor: pointer;
  color: #222021;
  align-items: start;
  display: flex;
}

.admin__input--ru {
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
}

.admin__category {
  border-bottom-right-radius: 24px;
  border-bottom-left-radius: 24px;
}

.admin__preview {
  border: 1px solid #222021;
  flex-wrap: wrap;
  align-items: center;
  gap: 5px;
  width: 100%;
  margin-bottom: 20px;
  display: flex;
}

.admin__preview div {
  height: 80px;
  position: relative;
}

.admin__preview button {
  position: absolute;
  top: 0;
  left: 0;
}

.admin__preview div img {
  width: 100%;
  height: 100%;
}

.admin__submit {
  color: #222021;
  cursor: pointer;
  background-color: #fee400;
  border: 2px solid #0000;
  border-radius: 48px;
  width: 190px;
  padding: 20px 0;
  transition: all .3s ease-in-out;
}

@media (hover: hover) {
  .admin__submit:hover {
    color: #222021;
    background-color: #0000;
    border: 2px solid #222021;
  }
}

.portfolio__categories {
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

.portfolio__tag:not(:last-child) {
  margin-right: 10px;
}

.portfolio__tag {
  cursor: pointer;
  color: #222021;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}

.portfolio__tag.active.portfolio__tag:after {
  width: 100%;
}

.portfolio__tag:after {
  content: "";
  background-color: #222021;
  width: 0%;
  height: 1px;
  transition: width .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: 0;
}

@media (hover: hover) {
  .portfolio__tag:hover.portfolio__tag:after {
    width: 100%;
  }
}

.portfolio__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.portfolio__item--img {
  object-fit: cover;
  width: 362px;
  height: 362px;
  margin-bottom: 40px;
  transition: all 1s ease-out;
}

.portfolio__item {
  flex-direction: column;
  align-items: center;
  max-width: 362px;
  max-height: 362px;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (hover: hover) {
  .portfolio__item:hover .portfolio__item--img {
    opacity: 1;
    filter: brightness(65%);
    transform: scale(1.21);
  }

  .portfolio__item:hover .portfolio__item--name {
    opacity: 1;
    transform: translateY(-20px);
  }
}

.portfolio__item--link {
  width: 100%;
  height: 100%;
  position: absolute;
}

.portfolio__item--name {
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  color: #d6d7dd;
  -webkit-user-select: none;
  user-select: none;
  opacity: 0;
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  transition: all 1s ease-in-out;
  position: absolute;
  bottom: 0;
}

@media (width <= 1200px) {
  .portfolio__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}

@media (width <= 781px) {
  .portfolio__item--img {
    object-fit: cover;
    width: 300px;
    height: 300px;
    margin-bottom: 40px;
    transition: all 1s ease-out;
  }

  .portfolio__item {
    max-width: 300px;
    max-height: 300px;
  }
}

@media (width <= 662px) {
  .portfolio__list {
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }
}
